@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.notificationbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    box-shadow: 0px 4px 4px #EBEBEB;
}

.notificationbar > h1 {
    padding-top: 20px;
    padding-bottom: 23px;
    margin: 0;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #0047AB;
}

.notificationbar > .icon {
    padding-top: 18px;
    padding-bottom: 21px;
    padding-right: 9px;
}
.navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 86px;
    -webkit-align-items: center;
            align-items: center;
    max-width: 1280px;
    padding: auto auto;
    margin: 0rem auto;
    overflow-x: hidden;
}

.logo {
    width: 200px;
    height: 38.44px;
}

span {
    color: #0047AB;
}


.contact > h1 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
}

ul {
    display: -webkit-flex;
    display: flex;
}

li {
    list-style: none;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #2B2D42;
    padding: 0rem 1rem;
    border-right:  1.5px solid #D7D4E2;
}

li:last-child {
    border-right: none;
}

li:hover {
    color: #0047AB;
    transition: 0.2s ease-in-out;
}

@media (max-width: 1330px) {
    li {
        font-style: 14px;
        padding: 0 0.8rem;
    }
}
.home {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: auto 0rem;
    overflow-x: hidden;
    background: linear-gradient(44.42deg, #FFF1E1 28.4%, #FFFFFF 52.55%);
}

.home__content > h1 , 
.home__content > p {
    color: #2B2D42;
}

.btn {
    background: #0047AB;
    color: #FFFFFF;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.12em;
}

button {
    outline: none;
    border: none;
    padding: 23px 31px;
}

.home__content {
    margin: auto;
    padding: 1rem;
}

.home__content > h1 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 48px;
    line-height: 59px;
}

.home__content > p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
}

.home__img {
    width: 54%;
}

.home__img > img {
    width: 100%;
    height: 100%;
}

@media (max-width: 1050px) {
    .home__content > h1 {
        font-size: 32px;
        line-height: 30px;
    }
    
    .home__content > p {
        font-size: 22px;
        line-height: 18px;
    }

    .home__content > button {
        padding: 19px;
        font-size: 14px;
    }

}

@media (max-width: 890px) {
    .home {
        -webkit-flex-direction: column-reverse;
                flex-direction: column-reverse;
    }

    .home__img {
        position: relative;
        width: 120vw;
        height: 60vh;
        overflow-x: hidden;
    }

    .home__img > img {
        position: absolute;
        width: 100%;
        height: 100%;
        left: -20vw;
    }

    .home__content {
        margin: 2rem auto;
    }

}

@media (max-width: 690px) {

    .home__img {
        position: relative;
        width: 140vw;
        height: 60vh;
        overflow-x: hidden;
    }

    .home__img > img {
        position: absolute;
        width: 100%;
        height: 100%;
        left: -20vw;
    }

    .home__content {
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: flex-start;
                align-items: flex-start;
        text-align: left;
    }

}


.location > h1 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    /* identical to box height */

    letter-spacing: 0.12em;

    color: #2B2D42;
}

.location {
    background: linear-gradient(208.56deg, #FFC03D -29.1%, #FFC107 64.78%);
    margin: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding: 1.4rem 0rem;
}
.services {
    max-width: 1300px;
    display: grid;
    place-items: center;
    margin: 70px auto;
    grid-template-columns: repeat(auto-fit, minmax(227px , 227px));
    grid-gap: 1em;
    overflow-x: hidden;
}

.service__intro { 
    display: grid;
    grid-column: 1/3;
}

.service__intro > p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 29px;
    letter-spacing: -0.007em;
    color: #2B2D42;
}

.service__intro > h1 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 37px;
    color: #2B2D42;
}

.service__intro > span {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 37px;
    color: #2B2D42;
}

.service__content > p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: -0.005em;
    color: #2B2D42;
}

.service__content > h1 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: -0.005em;
    color: #2B2D42;
}

.service , 
.contact {
    height: 345px;
    padding: 1rem 1.5rem;
}

.service {
    border-right: 1px solid #E5E5E5;;
}

.sm {
    display: none;
}

.lg {
    display: block;
}

@media (max-width: 900px) {
    .sm {
        display: block;
    }

    .lg{
        display: none;
    }

    .service {
        border: none;
    }

    .contact ,
    .service__intro {
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
        text-align: center;
    }

    .service__intro > p {
        font-size: 16px;
    }

    .service__intro > h1 {
        font-size: 24px;
    }

    p {
        font-family: Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 30px;
        letter-spacing: -0.005em;
    }
    
    h1 {
        font-family: Montserrat;
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;
        letter-spacing: -0.005em;
        color: #2B2D42;
    }

    
}
.credit {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding: 2.2rem 0rem;
}

.credit__content {
    padding-left: 1.5rem;
}
.images {
    display: grid;
    grid-template-columns: repeat(6,1fr);
    overflow-x: hidden;
}

.images > img {
    width: 278px;
    height: 278px;
}

.request {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    background: #395FA9;
}

.request__img {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 250px;
    height: 250px;
    padding-top: 1.5rem;
}

.request__content > h1 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 37px;
    color: #FFFFFF;
}

.request__img > img {
    width: 100%;
    height: 100%;
}

@media (max-width: 990px) {
    .request__content > h1 {
        font-size: 24px;
    }

    .request__content > .btn {
        padding: 1.5rem;
        font-size: 14px;
    }

    .request__img > img {
        width: 180px;
        height: 180px;
    }

}

@media (max-width: 800px) {
    .request{
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
    }

    .request__img {
        width: 200px;
        height: 200px;
    }
}
